.sb-badged-container__container {
  height: fit-content;
  position: relative;
  width: fit-content;
}

.sb-badged-container__badge {
  position: absolute;
  z-index: var(--sbt-stack-floating);
}

.sb-badged-container__top-left-badge {
  left: 0;
  top: 0;
  transform: translate(
    var(--sb-badged-container-top-left-translate-x, -15%),
    var(--sb-badged-container-top-left-translate-y, -15%)
  );
}

.sb-badged-container__top-right-badge {
  right: 0;
  top: 0;
  transform: translate(
    var(--sb-badged-container-top-right-translate-x, 15%),
    var(--sb-badged-container-top-right-translate-y, -15%)
  );
}

.sb-badged-container__bottom-left-badge {
  bottom: 0;
  left: 0;
  transform: translate(
    var(--sb-badged-container-bottom-left-translate-x, -7%),
    var(--sb-badged-container-bottom-left-translate-y, 7%)
  );
}

.sb-badged-container__bottom-right-badge {
  bottom: 0;
  right: 0;
  transform: translate(
    var(--sb-badged-container-bottom-right-translate-x, 15%),
    var(--sb-badged-container-bottom-right-translate-y, 15%)
  );
}
